<template>
  <v-container>
    <div class="d-flex justify-center mt-3">
      <p>recording stops in {{ curCount }} seconds</p>
    </div>
    <div class="d-flex justify-center mb-3">
      <div id="overlay">
        <video src="" muted playsinline id="webcam"></video>
        <canvas id="mask" v-show="isCamStarted"></canvas>
      </div>
    </div>

    <div class="my-3 d-flex justify-center">
      <v-btn @click="startCam" rounded class="mx-3" v-show="!isCamStarted"
        >start Cam</v-btn
      >
      <v-btn @click="stopCam" rounded class="mx-3" v-show="isCamStarted"
        >stop Cam</v-btn
      >
      <v-btn @click="predict" rounded class="mx-3" :disabled="!isCamStarted"
        >Predict</v-btn
      >
    </div>
    <v-divider></v-divider>
    <div>
      <h3>Your estimated Vital Signs</h3>
      <ul>
        <li>AGE: {{ AGE }}</li>
        <li>BMI: {{ BMI }}</li>
      </ul>
    </div>
  </v-container>
</template>

<script>
import BMIModel from "@/assets/js/BMI";
import webcam from "@/assets/js/webcam";
export default {
  name: "TFModels",
  data: () => ({
    camSize: [480, 480],
    imageSize: [224, 224],
    box: null,
    isCamStarted: false,
    maxCount: 5,
    curCount: 5,
    BMI: null,
    AGE: null,
  }),
  mounted: function () {
    console.log("mounted");
    // BMIModel.loadModel("http://localhost:5500/public/models/BMI/model.json");
    BMIModel.loadModel("/data-story/assets/dist/models/BMI/model.json");
    this.webcam = document.getElementById("webcam");
    this.mask = document.getElementById("mask");
    this.overlay = document.getElementById("overlay");
    this.adjustSize();
    this.drawBox();
  },
  methods: {
    startCam: function () {
      webcam.start(this.webcam);
      this.isCamStarted = true;
    },
    stopCam: function () {
      webcam.stop(this.webcam);
      this.isCamStarted = false;
    },
    predict: function () {
      const intv = setInterval(() => {
        if (this.curCount > 0) {
          BMIModel.predict(this.webcam, this.box, this.imageSize);
          this.curCount--;
        } else {
          clearInterval(intv);
          const results = BMIModel.output();
          this.BMI = `${results.BMI[0].toFixed(1)} +/- ${results.BMI[1].toFixed(
            1
          )}`;
          this.AGE = `${results.AGE[0].toFixed(0)} +/- ${results.AGE[1].toFixed(
            0
          )}`;

          this.curCount = this.maxCount;
        }
      }, 500);
    },

    drawBox: function () {
      const leftSpace = parseInt((this.camSize[0] - this.imageSize[0]) / 2);
      const topSpace = parseInt(0.3 * (this.camSize[1] - this.imageSize[1]));
      const y1 = topSpace / this.camSize[1];
      const x1 = leftSpace / this.camSize[0];
      const y2 = y1 + this.imageSize[1] / this.camSize[1];
      const x2 = x1 + this.imageSize[0] / this.camSize[0];
      this.box = [[y1, x1, y2, x2]];
      const ctx = this.mask.getContext("2d");
      ctx.beginPath();
      ctx.lineWidth = 4;
      ctx.strokeStyle = "red";
      ctx.rect(leftSpace, topSpace, this.imageSize[0], this.imageSize[1]);
      ctx.stroke();
    },
    adjustSize: function () {
      if (this.camSize[0] > window.screen.width - 24) {
        this.camSize = [window.screen.width - 24, window.screen.width - 24];
      }
      [this.mask.width, this.mask.height] = this.camSize;
      [this.webcam.width, this.webcam.height] = this.camSize;
      this.overlay.setAttribute(
        "style",
        `width : ${this.camSize[0]}px; height : ${this.camSize[1]}px; `
      );
    },
  },
};
</script>

<style scoped>
#overlay {
  position: relative;
  /* border: 1px black solid; */
}

#webcam {
  position: absolute;
}

#mask {
  position: absolute;
}
</style>