class webcam {
    constructor() { }
    start(elm) {
        const constraints = {
            video: { width: elm.width, height: elm.height, facingMode: 'user' },
            audio: false
        }
        navigator.mediaDevices.getUserMedia(constraints)
            .then((stream) => {
                elm.srcObject = stream;
                elm.play();
            })
    }
    stop(elm) {
        elm.srcObject.getTracks().forEach((track) => {
            track.stop();
        })
        elm.srcObject = null;
    }
}




export default new webcam();